export default {
  fullTime(data) {
    let date = new Date(data);
    let year = date.getUTCFullYear() + '-';
    let month = (date.getUTCMonth() + 1 < 10 ? '0' + (date.getUTCMonth() + 1) : date.getUTCMonth() + 1) + '-';
    let day = (date.getUTCDate() < 10 ? '0' + date.getUTCDate() : date.getUTCDate()) + ' ';
    return year + month + day
  },
  durationTime(begin,end){
    let beginDate = new Date(begin);
    let endDate = new Date(end);
    let beginDay = (beginDate.getUTCDate() < 10 ? '0' + beginDate.getUTCDate() : beginDate.getUTCDate()) + ' ';
    let endDay = (endDate.getUTCDate() < 10 ? '0' + endDate.getUTCDate() : endDate.getUTCDate()) + ' ';
    return endDay-beginDay + '天'
  }
}



