import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Index'
    },
    {
      path: '/Home',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      meta: { title: '主页' },
      children: [
        {
          path: '/Index',
          component: resolve => require(['../pages/Index.vue'], resolve),
          meta: { title: '首页' }
        },
        {
          path: '/Warning',
          component: resolve => require(['../pages/Warning.vue'], resolve),
          meta: { title: '预警信息' }
        },
        {
          path: '/GetContract',
          component: resolve => require(['../pages/GetContract.vue'], resolve),
          meta: { title: '持有仓单' }
        },
        {
          path: '/CustomerInfo',
          component: resolve => require(['../pages/CustomerInfo.vue'], resolve),
          meta: { title: '客户信息' }
        },
        {
          path: '/statistics',
          component: resolve => require(['../pages/Statistics.vue'], resolve),
          meta: { title: '统计信息' }
        },
        {
          path: '/NotCleardBusiness',
          component: resolve => require(['../pages/NotCleardBusiness.vue'], resolve),
          meta: { title: '未结清业务' }
        },
        {
          path: '/CleardBusiness',
          component: resolve => require(['../pages/CleardBusiness.vue'], resolve),
          meta: { title: '已结清业务' }
        },
        {
          path: '/AccountRecord',
          component: resolve => require(['../pages/AccountRecord.vue'], resolve),
          meta: { title: '出账记录' }
        },
        {
          path: '/CurrentWarning',
          component: resolve => require(['../pages/CurrentWarning.vue'], resolve),
          meta: { title: '当前预警' }
        },
        {
          path: '/HistoryWarning',
          component: resolve => require(['../pages/HistoryWarning.vue'], resolve),
          meta: { title: '历史预警' }
        },
      ]
    },
    {
      path: '/login',
      component: resolve => require(['../pages/Login.vue'], resolve)
    },
  ]
})



